form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

input {
  padding: 0.75rem;
  border: 1px solid #b7c5d3;
  border-radius: 4px;
  font-size: 1rem;
}

input:focus {
  border-color: #6375ff;
  outline: none;
  box-shadow: 0 0 5px rgba(108, 99, 255, 0.5);
}

button {
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  background-color: #6375ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #4759a3;
}

button:disabled {
  background-color: #c4c4c4;
  cursor: not-allowed;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

label {
  font-weight: bold;
  margin-bottom: 0.25rem;
}

.error {
  color: #e63946;
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

input.error-input {
  border-color: #e63946;
}
